import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, message } from 'antd';
import { createKnowledge, updateKnowledge, getKnowledgeById } from 'services/knowledgeUnit';
import { getQueryParamId } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    knowledgeName: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormPointLadderFour extends Component {
  state = {
    knowledgeId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    if (getQueryParamId()) {
      this.setState({ knowledgeId: getQueryParamId() });
      this.getDataById(getQueryParamId());
    }
  };

  getDataById = async id => {
    const res = await getKnowledgeById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        try {
          if (!this.state.knowledgeId) {
            await createKnowledge(values);
          } else {
            await updateKnowledge({ id: this.state.knowledgeId, ...values });
          }
          this.props.history.goBack();
        } catch (error) {
          message.error(`Có lỗi xãy ra!`);
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Cập nhật danh mục khối kiến thức' : 'Thêm danh mục khối kiến thức'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Tên kiến thức" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={this.props.history.goBack} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPointLadderFour {...props} />
  </Page>
));



