import { request } from 'utils';

const prefix = '/api/categories';


export const getKnowledges = ({
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
request({
  prefix,
  url: '/Knowledge',
  method: 'GET',
  params: {
    label,
    page,
    pageSize,
    orderBy,
    isPagingEnabled
  },
  data: {}
});


export const createKnowledge = ({label} = {}) =>
  request({
    prefix,
    url: '/Knowledge',
    method: 'POST',
    params: {},
    data: {label}
  });



export const getKnowledgeById = id =>
  request({
    prefix,
    url: `/Knowledge/${id}`,
    method: 'GET',
    params: {},
    data: {}
  });



export const updateKnowledge = ({ id, label } = {}) =>
  request({
    prefix,
    url: `/Knowledge/${id}`,
    method: 'PUT',
    params: {},
    data: { id, label }
  });



export const deleteKnowledge = id =>
  request({
    prefix,
    url: `/Knowledge/${id}`,
    method: 'DELETE',
    params: {},
    data: {}
  });




